import React, {Component} from 'react';
import '../../stylesheets/style.scss';
import logo from '../../ecolife.jpg';
const arrayNav =[
    {   
        id: 1,
        title: 'Home',
        url: ''
    },
    {   
        id: 2,
        title: 'Fresh Food',
        url: ''
    },
    {   
        id: 3,
        title: 'Fresh Vegetables',
        url: '',
        subArray:
        [
            {   
                id: 1,
                subTitle: 'Potatoes',
                url: ''
            },
            {   
                id: 2,
                subTitle: 'Onions',
                url: ''
            },
            {   
                id: 3,
                subTitle: 'Carrots',
                url: ''
            },
            {   
                id: 4,
                subTitle: 'Broccoli',
                url: ''
            },
            {   
                id: 5,
                subTitle: 'Cabbages',
                url: ''
            }
        ]
    },
    {   
        id: 4,
        title: 'Prices Drop',
        url: ''
    },
    {   
        id: 5,
        title: 'Contact Us',
        url: ''
    },
];
class Menu extends Component {
    constructor(props) {
        super(props)
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
        this.state = {
            isToggle : 0,
        }
    }

    componentDidMount() {
        console.log("Render first.")
    }

    handleMouseEnter = (id) => {
        this.setState({isToggle:id});
    }
    handleMouseLeave = (id) => {
        this.setState({isToggle:null});
        // console.log("Toogle: ", this.state.isToggle);
    }
    render() {
        
        const {isToggle} = this.state;

        // console.log("Toogle RENDER: ", isToggle);

        return(
            <div className= "nav">
                <ul className="list">
                    {   
                        arrayNav.map(item => (
                            <>
                            <li className= "item" onMouseLeave = {() => {this.handleMouseLeave(item.id)}} onMouseEnter = {() => {this.handleMouseEnter(item.id)}} key = {item.id}><a>{item.title}</a>
                            {isToggle == item.id && item.subArray && 
                            <ul className="list-sub-item">
                                {
                                    item.subArray.map(subItem => (
                                        <li className="sub-item" key= {subItem.id}>
                                            <a>{subItem.subTitle}</a>
                                        </li>
                                    ))
                                }
                            </ul>
                            }
                            </li>
                            </>
                        ))
                    }
                </ul>
                
            </div>
        )
    }
}
const Header = () => {
    return(
        <>
        <div className="header">
                <div className="wrapper_menu">
                    <h1  className="logo"><a><img src= {logo}/></a></h1>
                    <Menu/>
                </div>
        </div>
        </>
    )
}
export default Header;
