import React,{Component} from 'react';
import {ApiService} from '../Api/getApi';
const api = new ApiService();
class Product extends Component {
	constructor(props) {
		super(props)
		this.state = {
				product : []
		}
	}
	async componentDidMount() {
		const product = await api.get('products')
		if (!product) {
			console.log('loading');
		} else if (product.length === 0) {
			return <p>empty</p>;
		}else {
			return this.setState({product: product});
		}
	}

	componentDidUpdate() {
		console.log('update');
	}
	render() {
		const {product} = this.state;
		return(
				<div className="product">
					{
						product && (
							<List data={product}/>
						)
					}
				</div>
		)
	}
}
const List = ({data}) => {
	return(
		<ul className="list-product">
		{data.map((item, index) => (
		<li className="list-product-item" key={index}>
				<div className="wrapper-contents">
					<figure className="left text-center"><img className="item-img" src={item.cover}/></figure>
					<div className="right">
						<span className="title text-left">{item.name}</span>
						{
							item.sales == 0 ? (
								<span className="price">${item.price}</span>
							) :
							<>
							<span className="price cancel">${item.price}</span>
							<span className="sales">
							{
								item.sales != 0 &&
								(percentCalculation(item.sales, item.price))
							}
							</span>
							</>
						}
						{	
							(
								<fieldset class="rating">
									<label class = "full" for="activestar5" id="1" title="Awesome - 5 stars"></label>
									<label class = "full" for="activestar4" id="2" title="Pretty good - 4 stars"></label>
									<label class = "full" for="activestar3" id="3" title="Meh - 3 stars"></label>
									<label class = "full" for="activestar2" id="4" title="Kinda bad - 2 stars"></label>
									<label class = "full" for="activestar1" id="5" title="Sucks big time - 1 star"></label>
								</fieldset>
							)
						}
					</div>
				</div>
				<button onClick= {() => {addItem(item.id)}}>Add to cart</button>
		</li>
		))}
		</ul>
	)
};
const percentCalculation = (total,price) => {
	if (total.indexOf('%') == -1)  {
		const priceFormat = new Intl.NumberFormat('en-CA', 
    {style: 'currency', currency:'CAD'}).format(parseFloat(price) + parseFloat(total));
		return priceFormat;
	}else {
		const rlTotal = total.replace(/[-/\\^$*+?.()|[\]{}%]/g,'');
		return  '$' + (parseFloat(price)-(parseFloat(price) * parseFloat(rlTotal))/100);
	}
}
const ratingAddCss = (num) => {
	switch(num) {
		case 5 :
			document.getElementById("1").style.color= "#ffb25d";
			break;
		case 4 :
			document.getElementById("2").style.color= "#ffb25d";
			break;
		case 3 :
			document.getElementById("3").style.color= "#ffb25d";
			break;
		case 2 :
			document.getElementById("4").style.color= "#ffb25d";
			break;
		case 1 :
			document.getElementById("5").style.color= "#ffb25d";
			break;
		default :
			break;
	}
}
const addItem = (id) => {
	console.log(id);
}
export default Product;
